body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 960px 1fr;
  align-items: center;
}

.main {
  grid-column: 2 / span 1;
}
.quote {
  margin-bottom: 5em;
}

.quote--quote {
  font-size: 4em;
  font-weight: bold;
}

.quote--author {
  font-size: 2em;
  align-self: start;
}

.btn {
  background: none;
  border-radius: 4px;
  font-size: 2em;
  padding: 0.3em 0.5em;
  cursor: pointer;
  margin-bottom: 20px;
}

.btn:hover {
  font-weight: bold;
}

.quote--tweet {
  display: block;
  font-size: 1.2em;
  text-decoration: none;
  font-style: italic;
}

